"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { toast } from "react-hot-toast";
import { ViewIcon, HideIcon } from "@/components/icons/Icons";
import { signupApi } from "@/lib/apiClient";
import { loginPage } from "@/app/routes";
import { userSignupSchema } from "@/components/validation";

import "../auth-common.css";
import "./signup.css";

import {
  TITLE_USER_SIGNUP,
  SUBTITLE_USER_SIGNUP,
  LABEL_NAME,
  LABEL_EMAIL,
  LABEL_PASSWORD,
  LABEL_CONFIRM_PASSWORD,
  PH_NAME,
  PH_USER_EMAIL,
  PH_PASSWORD,
  PH_CONFIRM_PASSWORD,
  TEXT_SIGNUP,
  TEXT_SIGNIN,
  TEXT_NO_ACCOUNT,
  ROLE_USER,
  MSG_SIGNUP_SUCCESS,
  MSG_ERR_SIGNUP_FAILED,
} from "@/const-value/config-message/page";

export default function UserSignupPage() {
  const router = useRouter();
  const [showPass, setShowPass] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);

  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
    type: ROLE_USER,
  });

  const onSubmit = async (e) => {
    e.preventDefault();

    try {
      await userSignupSchema.validate(form, { abortEarly: false });
      const res = await signupApi(form);

      if (!res.status) return toast.error(MSG_ERR_SIGNUP_FAILED);

      toast.success(MSG_SIGNUP_SUCCESS);
      router.push(loginPage);
    } catch (err) {
      toast.error(err.errors?.[0] || MSG_ERR_SIGNUP_FAILED);
    }
  };

  return (
    <div className="auth-shell">
      <div className="auth-left d-none d-lg-flex">
        <img src="/images/auth-signup.png" alt="signup" />
      </div>

      <div className="auth-right">
        <div className="auth-card signup-card">
          <h1 className="auth-title">{TITLE_USER_SIGNUP}</h1>
          <p className="auth-sub">{SUBTITLE_USER_SIGNUP}</p>

          <form onSubmit={onSubmit}>
            <label className="auth-label">{LABEL_NAME}</label>
            <input
              className="auth-input"
              placeholder={PH_NAME}
              onChange={(e) => setForm({ ...form, name: e.target.value })}
            />

            <label className="auth-label">{LABEL_EMAIL}</label>
            <input
              className="auth-input"
              placeholder={PH_USER_EMAIL}
              onChange={(e) => setForm({ ...form, email: e.target.value })}
            />

            <label className="auth-label">{LABEL_PASSWORD}</label>
            <div className="auth-pass-wrap">
              <input
                className="auth-input"
                type={showPass ? "text" : "password"}
                placeholder={PH_PASSWORD}
                onChange={(e) => setForm({ ...form, password: e.target.value })}
              />
              <span
                className="auth-pass-toggle"
                onClick={() => setShowPass(!showPass)}
              >
                {showPass ? <ViewIcon /> : <HideIcon />}
              </span>
            </div>

            <label className="auth-label">{LABEL_CONFIRM_PASSWORD}</label>
            <div className="auth-pass-wrap signup-confirm">
              <input
                className="auth-input"
                type={showConfirm ? "text" : "password"}
                placeholder={PH_CONFIRM_PASSWORD}
                onChange={(e) =>
                  setForm({ ...form, confirmPassword: e.target.value })
                }
              />
              <span
                className="auth-pass-toggle"
                onClick={() => setShowConfirm(!showConfirm)}
              >
                {showConfirm ? <ViewIcon /> : <HideIcon />}
              </span>
            </div>

            <button className="auth-btn">{TEXT_SIGNUP}</button>

            <div className="auth-footer">
              {TEXT_NO_ACCOUNT} <a href={loginPage}>{TEXT_SIGNIN}</a>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
